body{
    background: #fdb54a;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0%;
    padding: 1.5rem;
}
.heading{
    color: #4FA3A5;
    font-size: 4rem;
    padding-bottom: 0%;
    margin-bottom: 0%;
    margin-top: 1%;
}
.heading:hover{
    color: #FDAE38;
}
.blurLine{
    border-bottom: 0.3rem solid #4FA3A5;
}
.App{
    background: white;
    margin: 0% 20%;
    width: 60%;
    border-bottom: 1rem solid #4FA3A5;
    box-shadow: 0px 0px 5px white;
}

.formGrp1{
    display: flex;
    text-align: left;
    padding: 0rem 3rem;
    gap: 10%;
}
.formGrp1 div{
    width: 50%;
}
.formGrp1 input{
    width: 100%;
    border: none;
    border-bottom: 2px solid #4FA3A5;
    margin-bottom: 1rem;   
    font-size: 1rem;
    padding: 0.3rem 0.6rem;
}

.completeForm{
    display: flex;
    flex-direction: column;
    padding: 2rem;
    font-size: 1.5rem;
    color: rgb(95, 93, 93);
}

.formGrp2 input{
    width: 100%;
    border: none;
    border-bottom: 2px solid #4FA3A5;
    margin-bottom: 1rem;
    font-size: 1rem;
    padding: 0.3rem 0.6rem;
}
.formGrp2{
    text-align: left;
    padding: 0rem 3rem;
}
.bottom-row{
    display: flex;
    gap: 2rem;
}
.bottom-row div{
    width: 50%;
}
.Gender{
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
}
.Gender input{
    width: 14px;
    height: 14px;
    margin: 0%;
}

/* .Gender label{
    margin-right: 10%;
} */
.formGrp2 textarea{
    width: 100%;
    height: 4rem;
    border: 1.5px solid #4FA3A5;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    font-size: 1rem;
    padding: 0.3rem 0.6rem;
}
.completeForm button{
    align-items: center;
}
.btnBox{
    width: 100%;
}
.btn{
    width: 40%;
    font-size: 1.5rem;
    background: #4FA3A5;
    color: white;
    border: none;
    font-weight: 600;
    padding: 0.5rem 1rem;
    border-radius: 1.5rem;
    cursor: pointer;
}
.btn:hover{
    background: #FDAE38;
    color: white;
}
.formGrp1 input:focus{
    outline: none;
}
.formGrp2 input:focus{
    outline: none;
}
.formGrp2 textarea:focus{
    outline: none;
}
.pop-box{
    position: absolute;
    background-color: #80d2d3;
    height: 200px;
    width: 30%;
    top: 30%;
    right: 35%;
}
.pop-header{
    display: flex;
    justify-content: space-between;
    padding: 0rem 0.5rem;
    border-bottom: 2px solid white;
    height: 4rem;
}
.pop-logo{
    background-image: url("./fb-bg.png");
    background-size: cover;
    background-position: center;
    height: 4rem;
    width: 8rem;
    margin: 0%;
}
.name-logo{
    color: white;
    font-size: 1rem;
    padding: 0.5rem;
    font-weight: bold;
}
.cross{
    font-size: 1.5rem;
    color: white;
}
.cross:hover{
    cursor: pointer;
}
.pop-box p{
    color: white;
    font-size: 1.5rem;
    margin: 1rem;
}